
.s_carousel[data-v-087c648c]{
	width: 100%;
	padding: 15px 0;
}
.s_carousel_container[data-v-087c648c]{
	position: relative;
	height: 200px;
}
.s_carousel_item[data-v-087c648c]{
	position: absolute;
	width: 540px;
	height: 100%;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	-webkit-transition: width 0.5s, height 0.5s, left 0.5s, right 0.5s;
}
.s_carousel_item > div[data-v-087c648c]{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.3);
}
.s_carousel_item > img[data-v-087c648c]{
	width: 100%;
	height: 100%;
}
.s_carousel_left[data-v-087c648c],
.s_carousel_right[data-v-087c648c]{
	z-index: 2;
	width: 500px;
	height: 185px;
}
.s_carousel_center[data-v-087c648c],
.s_carousel_back[data-v-087c648c]{
}
.s_carousel_left[data-v-087c648c]{
	/*left: 0;*/
	left: -260px;
}
.s_carousel_right[data-v-087c648c]{
	/*right: 0;*/
	right: -260px;
}
.s_carousel_center[data-v-087c648c]{
	z-index: 3;
}
.s_carousel_center > div[data-v-087c648c]{
	background-color: transparent;
}
.s_carousel_back[data-v-087c648c]{
	z-index: 1;
	width: 460px;
	height: 170px;
}
.s_carousel_footer .s_carousel_trun[data-v-087c648c]{
	cursor: pointer;
	display: inline-block;
	width: 20px;
	height: 3px;
	border-radius: 3px;
	margin: 0 5px;
	background-color: rgba(255, 255, 255, 0.3);
}
.s_carousel_footer .s_carousel_trun_selected[data-v-087c648c],
.s_carousel_footer .s_carousel_trun[data-v-087c648c]:hover{
	background-color: rgba(255, 255, 255, 0.7);
}




